import { useState, lazy, Suspense } from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";

import AppHeader from "../appHeader/AppHeader";
import Spinner from "../spinner/Spinner";

const Pages404 = lazy(() => import('../pages/404'));
const MainPage = lazy(() => import('../pages/MainPage'));
const ComicsPage = lazy(() => import('../pages/ComicsPage'));
const SingleComicLayout = lazy(() => import('../pages/SingleComicLayout'));
const SingleCharacterLayout = lazy(() => import('../pages/SingleCharacterLayout'));
const SinglePage = lazy(() => import('../pages/SinglePage')); 



const App = () => {
    const [pageUrl, setPageUrl] = useState(window.location.href);

    return (
        <Router>
            <div className="app">
                <AppHeader/>
                <main>
                    <Suspense fallback={<Spinner/>}>
                        <Routes>
                            <Route path="/marvel-react-project" element={<MainPage pageUrl={pageUrl} setPageUrl={setPageUrl}/>}/>                           
                            <Route path="/marvel-react-project/comics" element={<ComicsPage/>}/>
                            <Route path="/marvel-react-project/comics/:id" element={<SinglePage Component={SingleComicLayout} dataType='comic' pageUrl={pageUrl}/>}/>
                            <Route path="/marvel-react-project/char/:id" element={<SinglePage Component={SingleCharacterLayout} dataType='character'/>}/>
                            <Route path="*" element={<Pages404/>}/>
                        </Routes>
                    </Suspense>
                </main>          
            </div>
        </Router>
    )
}

export default App;